import * as React from "react";
import { useParams } from "react-router-dom";
import { MEMORIES_DATA } from "../memories_data";
import BasePage from "../base_page";
import ImageListItem from "@mui/material/ImageListItem";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Divider from "@mui/material/Divider";
import { alpha } from "@mui/material/styles";

import IconButton from "@mui/material/IconButton";
import LaunchIcon from "@mui/icons-material/Launch";
import { ImageLink } from "./utils";
import { useLocation, useNavigate } from "react-router-dom";
import { LanguageDialog, LanguageChooser } from "../../components/language_dialog";

export default function Memory() {
    let params = useParams();
    let location = useLocation();
    let navigate = useNavigate();
    const [language, setLanguage] = React.useState(null);

    console.log(location);

    React.useEffect(() => {
        if (location.state) {
            setLanguage(location.state.language);
        }
    }, [location]);

    const setEnglish = () => {
        navigate(location.pathname, { state: { language: "english" } });
    };

    const setBengali = () => {
        navigate(location.pathname, { state: { language: "bengali" } });
    };

    return language ? (
        <BasePage language={language}>
                <LanguageChooser
                    language={language}
                    setEnglish={setEnglish}
                    setBengali={setBengali}
                />
            <MemoryContent memory={params.memoryId} language={language} />
        </BasePage>
    ) : (
        <LanguageDialog setEnglish={setEnglish} setBengali={setBengali} />
    );
}

function MemoryContent({ memory, language, ...otheProps }) {
    let memoryDetails = getMemoryDetails(parseInt(memory, 10));
    let images = require.context("../../images/birthday_pics", true);
    return (
        <Card
            sx={{
                maxWidth: "100%",
                m: 2,
                p: 2,
                b: 2,
                minHeight: "100vh",
                backgroundColor: alpha("#fff", 0.8),
                borderRadius: 10,
            }}
            className="bg-transparent"
        >
            <CardHeader
                title={memoryDetails.title[language]}
                subheader={memoryDetails.date[language]}
                action={
                    <>
                        <Box
                            sx={{
                                display: "flex",
                                justifyContent: "center",
                                paddingTop: "0.6rem",
                            }}
                        >
                            {language === "english" ? (
                                <Typography variant="h5" color="inherit">
                                    Theme
                                </Typography>
                            ) : (
                                <Typography variant="h5" color="inherit">
                                    থিম
                                </Typography>
                            )}
                        </Box>
                        <Box
                            sx={{
                                display: "flex",
                                justifyContent: "center",
                            }}
                        >
                            <Typography variant="body1" color="text.secondary">
                                {memoryDetails.thumbnail.text[language]}
                            </Typography>
                        </Box>
                    </>
                }
            />
            <Divider />
            <Grid
                container
                justifyContent="space-between"
                sx={{
                    mt: 1,
                }}
            >
                <MemoryImageList
                    details={memoryDetails}
                    images={images}
                    language={language}
                />
                <MemoryText text={memoryDetails.text[language]} />
            </Grid>
        </Card>
    );
}

function MemoryImageList({ details, images, language }) {
    return (
        <Grid
            item
            lg={6}
            container
            justifyContent="center"
            sx={{ border: 2, borderRadius: 10, mb: 2 }}
        >
            {details.pics.map((item, index) => (
                <ImageListItem key={index} sx={{ p: 1 }}>
                    <ImageLink
                        details={details}
                        picnum={index}
                        context={images}
                        language={language}
                    >
                        <IconButton
                            sx={{ position: "absolute", right: 10 }}
                            component="div"
                        >
                            <LaunchIcon />
                        </IconButton>
                        <Box
                            component="img"
                            sx={{
                                borderRadius: 5,
                                border: 2,
                                borderColor: "muted.main",
                                "&:hover": {
                                    border: 5,
                                    borderColor: "secondary.main",
                                },
                            }}
                            src={`${images(item).default}`}
                            alt="test"
                            width="250px"
                            fit="crop"
                            auto="format"
                            loading="lazy"
                        />
                    </ImageLink>
                </ImageListItem>
            ))}
        </Grid>
    );
}

function MemoryText({ text }) {
    return (
        <Grid item lg={5} sx={{ border: 2, borderRadius: 10, mb: 2, width: 1 }}>
            {text.map((para, index) => {
                return (
                    <Box sx={{ p: 3 }}>
                        <Typography
                            variant="h5"
                            key={index}
                            color="primary.main"
                        >
                            {para.title}
                        </Typography>
                        <Box>
                            {para.text.map((chunk, ind) => {
                                return (
                                    <Typography paragraph key={ind}>
                                        {chunk}
                                    </Typography>
                                );
                            })}
                        </Box>
                    </Box>
                );
            })}
        </Grid>
    );
}

function getMemoryDetails(memoryId) {
    return MEMORIES_DATA.find((memory) => memory.key === memoryId);
}
