import * as React from "react";
import { Branding } from "../brand";
import { TopBarGridContainer } from "./utils";
import { MiscLinks } from "./misclinks";
import { TopBarButton } from "./utils";

import { Link } from "react-router-dom";

import Grid from "@mui/material/Grid";

export default function TopBar({ language }) {
    return (
        <TopBarGridContainer>
            <Link style={{ textDecoration: "none" }} to="/">
                <Branding />
            </Link>
            <Grid item sx={{ paddingTop: "0.7rem" }}>
                {language ? (
                    <Link
                        style={{ textDecoration: "none" }}
                        to={`/memories`}
                        state={{language:`${language}`}}
                    >
                        <TopBarButton
                            text="My Memories"
                            borderColor="secondary.main"
                        />
                    </Link>
                ) : (
                    <Link style={{ textDecoration: "none" }} to="/memories">
                        <TopBarButton
                            text="My Memories"
                            borderColor="secondary.main"
                        />
                    </Link>
                )}
            </Grid>
            <MiscLinks />
        </TopBarGridContainer>
    );
}
