import * as React from "react";

import BasePage from "../base_page";
import { MEMORIES_DATA } from "../memories_data";
import { MemoriesPreview } from "./utils";
import { useLocation, useNavigate } from "react-router-dom";
import { LanguageDialog, LanguageChooser } from "../../components/language_dialog";

export default function Memories() {
    const location = useLocation();
    const navigate = useNavigate();
    const [language, setLanguage] = React.useState(null);

    React.useEffect(() => {
        if (location.state) {
            setLanguage(location.state.language);
        }
    }, [location]);

    const setEnglish = () => {
        navigate(location.pathname, { state: { language: "english" } });
    };

    const setBengali = () => {
        navigate(location.pathname, { state: { language: "bengali" } });
    };

    return language ? (
        <BasePage>
                <LanguageChooser
                    language={language}
                    setEnglish={setEnglish}
                    setBengali={setBengali}
                />
            <MemoriesPreview language={language} memories={MEMORIES_DATA} />
        </BasePage>
    ) : (
        <LanguageDialog setEnglish={setEnglish} setBengali={setBengali} />
    );
}
