import React from "react";
import Grid from "@mui/material/Grid";
import { Link } from "react-router-dom";

import MemoryCard from "./memory_card";

function MemoryCardLink({ memory, language, images }) {
    let pathname = "/memory/" + memory.key;

    return (
        <Link
            style={{ textDecoration: "none" }}
            to={pathname}
            state={{
                language: language 
            }}

            key={memory.key}
        >
            <MemoryCard
                key={memory.key}
                title={memory.title[language]}
                date={memory.date[language]}
                pic={images(memory.thumbnail.image)}
                text={memory.thumbnail.text[language]}
                language={language}
            />
        </Link>
    );
}

export function MemoriesPreview({ language, memories }) {
    let images = require.context("../../images/birthday_pics", true);
    return (
        <Grid container sx={{ p: 1, m: 1, borderRadius: 2 }}>
            {memories.map((memory, index) => {
                return (
                    <Grid
                        item
                        lg={3}
                        md={4}
                        sm={6}
                        xs={12}
                        key={index}
                        justifyContent="space-around"
                    >
                        <MemoryCardLink
                            memory={memory}
                            language={language}
                            images={images}
                        />
                    </Grid>
                );
            })}
        </Grid>
    );
}
