import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";

export const FooterText = ({ children }) => (
    <Typography sx={{ paddingX: 2, color: "muted.main", display: "inline" }}>
        {children}
    </Typography>
);

export const FooterGridContainer = ({ justify, children }) => (
    <Grid
        container
        alignItems="center"
        justifyContent={justify || "space-between"}
        sx={{ p: 1, borderTop: 4, borderColor: "secondary.main" }}
    >
        {children}
    </Grid>
);

export const SocialLink = ({to, color, children}) => (
    <Link
        underline="none"
        href={to}
        rel="noreferrer noopener"
        target="_blank"
        sx={{ px: 1, color: `${color}` }}

    >
        {children}
    </Link>
);
