import * as React from "react";
import Box from "@mui/material/Box";
import KeyboardArrowLeft from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRight from "@mui/icons-material/KeyboardArrowRight";
import SwipeableViews from "react-swipeable-views";
import { autoPlay } from "react-swipeable-views-utils";
import IconButton from "@mui/material/IconButton";
import bday_bg from "../images/birthday_bg.jpg";

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

function MemorySwipeableView({ picnum, details, images }) {
    const [activeStep, setActiveStep] = React.useState(picnum);
    const maxSteps = details.pics.length;

    React.useEffect(() => {
        setActiveStep(picnum);
    }, [picnum]);

    const handleNext = () => {
        setActiveStep((prevActiveStep) => (prevActiveStep + 1) % maxSteps);
    };

    const handleBack = () => {
        setActiveStep(
            (prevActiveStep) => (prevActiveStep - 1 + maxSteps) % maxSteps
        );
    };

    const handleStepChange = (step) => {
        setActiveStep(step);
    };

    return (
        <Box
            sx={{
                display: "flex",
                flexGrow: 1,
                maxWidth: "100%",
                justifyContent: "space-between",
                backgroundImage: `url(${bday_bg})`,
            }}
            className="background"
        >
            <AutoPlaySwipeableViews
                index={activeStep}
                onChangeIndex={handleStepChange}
                enableMouseEvents
                animateTransitions
                interval={10000}
                onClick={handleNext}
                springConfig={{
                    duration: "0.7s",
                    easeFunction: "cubic-bezier(0.83, 0, 0.17, 1)",
                    delay: "0s",
                }}
            >
                {details.pics.map((step, index) => (
                    <Box
                        key={step}
                        component="img"
                        sx={{
                            display: "block",
                            height: "100vh",
                            width: "100%",
                            resize: "horizontal",
                            objectFit: "contain",
                            backgroundImage: `url(${bday_bg})`,
                        }}
                        src={`${images(step).default}`}
                        className="background"
                    />
                ))}
            </AutoPlaySwipeableViews>
            <IconButton
                edge="start"
                color="inherit"
                aria-label="close"
                onClick={handleBack}
                sx={{
                    position: "absolute",
                    top: "50%",
                    left: 0,
                }}
            >
                <KeyboardArrowLeft
                    sx={{
                        fill: "white",
                        fontSize: "4.5rem",
                    }}
                />
            </IconButton>
            <IconButton
                edge="start"
                color="inherit"
                aria-label="close"
                onClick={handleNext}
                sx={{
                    position: "absolute",
                    top: "50%",
                    right: 0,
                }}
            >
                <KeyboardArrowRight
                    sx={{
                        fill: "white",
                        fontSize: "4.5rem",
                    }}
                />
            </IconButton>
        </Box>
    );
}

export default MemorySwipeableView;
