import React from "react";
import ImageRow from "./image_row";
import images from "./images_list";

import "./background.css";

class Background extends React.Component {
    constructor() {
        super();
        this.picDim = 128;
        this.state = {
            width: window.innerWidth,
            height: window.innerHeight,
        };
        this.chunk = this.chunk.bind(this);
        this.precalculate = this.precalculate.bind(this);
        this.getRandomArbitrary = this.getRandomArbitrary.bind(this);
    }

    handleResize = (e) => {
        console.log("I am handling resize!!");
        this.setState({
            width: window.innerWidth,
            height: window.innerHeight,
        });
    };

    getRandomArbitrary(min, max) {
        return Math.trunc(Math.random() * (max - min) + min);
    }

    chunk(arr, chunkSize, chunks) {
        var row = [];
        var randIndex = 0;

        for (let i = 0; i < chunks; i += 1) {
            randIndex = this.getRandomArbitrary(0, arr.length - chunkSize);
            row.push(arr.slice(randIndex, randIndex + chunkSize));
        }

        return row;
    }

    precalculate() {
        if (window.innerWidth < 800) {
            this.picDim = 64;
        }
        const picsPerRow = Math.floor(window.innerWidth / this.picDim);
        const picsPerColumn = Math.floor(window.innerHeight / this.picDim);
        let picRows = this.chunk(images, picsPerRow, picsPerColumn);
        let pictures = picRows.map((picRow, i) => {
            return (
                <ImageRow
                    key={i}
                    images={picRow}
                    rowNum={i}
                    width={this.picDim}
                    height={this.picDim}
                />
            );
        });
        return pictures;
    }

    componentDidMount() {
        window.addEventListener("resize", this.handleResize);
    }

    render() {
        const pictures = this.precalculate();
        return (
                <div className="bg justify-content-center">
                    {this.props.children}
                    {pictures}
                </div>
        );
    }
}

export default Background;
