import * as React from "react";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";

export const TopBarGridContainer = ({ justify, children }) => (
    <Grid
        container
        alignItems="center"
        justifyContent={justify || "space-between"}
        sx={{ p: 1, borderBottom: 4, borderColor: "secondary.main" }}
    >
        {children}
    </Grid>
);

export const TopBarButton = ({ text, onClick, borderColor }) => (
    <Button
        sx={{
            color: "button.main",
            border: 2,
            borderColor: borderColor || "transparent",
            borderRadius: 5,
        }}
        variant="outlined"
        onClick={onClick}
    >
        <Typography variant="h6">{text}</Typography>
    </Button>
);

const CloseButton = ({ onClick }) => (
    <IconButton
        edge="start"
        onClick={onClick}
        aria-label="close"
        sx={{
            fontSize: "3.5rem",
            position: "absolute",
            top: 0,
            right: 0,
        }}
    >
        <CloseIcon sx={{ fontSize: "3rem", fill: "muted.main",}} />
            
    </IconButton>
);

const TopBarDialog = ({ text, open, onClose, onClick, children, wdth }) => (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth={wdth}>
        <DialogTitle>
            {text}
            <CloseButton onClick={onClick} />
        </DialogTitle>
        <DialogContent dividers>{children}</DialogContent>
    </Dialog>
);

export function TopBarLink({ text, children, wdth }) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <span>
            <TopBarButton text={text} onClick={handleClickOpen} />
            <TopBarDialog
                text={text}
                open={open}
                wdth={wdth}
                onClose={handleClose}
                onClick={handleClose}
            >
                {children}
            </TopBarDialog>
        </span>
    );
}
