import nabarun_photo from "../images/blog_pic.jpg";
import sagnika_photo from "../images/sagnika.jpg";
import gogol_photo from "../images/gogol.jpg";


import "./about.css";

import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import useMediaQuery from '@mui/material/useMediaQuery';

const about_data = {
    gogol: {
        name: "Shlok Chatterjee (Gogol)",
        image: gogol_photo,
        text: ["CMO (Chief Motivation Officer)", "CCC (Chief Chaos Creator)"],
    },
    nabarun: {
        name: "Nabarun Chatterjee",
        image: nabarun_photo,
        text: [
            "Full time father to my 1 year old son and husband to my loving wife . Site Reliability Engineer by profession. Memory making travel and finger licking food are what I live for.",
        ],
    },
    sagnika: {
        name: "Sagnika Dasgupta",
        image: sagnika_photo,
        text: [
            "I don't remember my life from 1 year back. Now it is mostly Gogol and a little bit of food, travel, painting, writing and other things.",
        ],
    },
};

const AboutCard = ({ data }) => (
    <Card sx={{ minWidth: 250, maxWidth: 350, p: 1, m: 1 }}>
            <CardMedia
                component="img"
                height="350"
                image={data.image}
                alt={data.name}
                style={{
                    border: "5px solid rgb(14,81,106)",
                }}
            />
            <CardContent>
                <Typography variant="h6" sx={{py:1}}>
                    {data.name}
                </Typography>
                {data.text.map((part, index) => {
                    return (
                        <Typography key={index} variant="body2" color="text.secondary">
                            {part}
                        </Typography>
                    );
                })}
            </CardContent>
    </Card>
);

function About() {
    const reorient = useMediaQuery((theme) => theme.breakpoints.down('lg'));
    return reorient ? (
        <Grid container justifyContent="center" alignItems="center">
            <Grid item align="center" xs={12}>
                <AboutCard key="gogol" data={about_data.gogol} />
            </Grid>
            <Grid item align="center">
                <AboutCard key="nabarun" data={about_data.nabarun} />
            </Grid>
            <Grid item align="center">
                <AboutCard key="sagnika" data={about_data.sagnika} />
            </Grid>
        </Grid>

    ):(
        <Grid container justifyContent="space-between">
            <Grid item align="center">
                <AboutCard key="nabarun" data={about_data.nabarun} />
            </Grid>
            <Grid item align="center">
                <AboutCard key="gogol" data={about_data.gogol} />
            </Grid>
            <Grid item align="center">
                <AboutCard key="sagnika" data={about_data.sagnika} />
            </Grid>
        </Grid>
    );
}


export default About;
