import React from 'react';


class ImageRow extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            images: props.images,
        }

        this.permute = this.permute.bind(this);
        this.getRandomArbitrary = this.getRandomArbitrary.bind(this);
    }

    componentDidMount() {
        this.timerID = setInterval(
            () => this.permute(),
            5000
        );
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    getRandomArbitrary(min, max) {
        return Math.trunc(Math.random() * (max - min) + min);
    }

    permute() {
        this.setState(function(state, props) {
            let images = props.images;
            const ifChange = this.getRandomArbitrary(1, props.images.length);
            if (ifChange % 2 === 0) {
                const first = this.getRandomArbitrary(1, props.images.length);
                const second = this.getRandomArbitrary(1, props.images.length);
                const temp = images[first];
                images[first] = images[second];
                images[second] = temp;
                return {
                    images: images,
                    rowNum: props.rowNum,
                }
            }
        })
    }

    render() {
        let images = require.context('../images/bg', true);
        return (
            <div className="picrow" key={this.state.rowNum}>
            {this.state.images.map(pic => {
                return (
                    <img src={images(pic.pic).default} key={pic.uuid} alt={pic.uuid} width={this.props.width} height={this.props.height} />
                )
            })}
            </div>
        )
    }
}

export default ImageRow;
