import Dialog from "@mui/material/Dialog";
import Divider from "@mui/material/Divider";
import DialogTitle from "@mui/material/Divider";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import { LanguageChanger } from "./common";

export const LanguageChooser = ({language, setEnglish, setBengali}) => (
            <Divider>
                <LanguageChanger
                    language={language}
                    setEnglish={setEnglish}
                    setBengali={setBengali}
                />
            </Divider>
);


const LanguageButton = ({ onClick, text }) => (
    <Button sx={{ border: 2 }} onClick={onClick}>
        {text}
    </Button>
);

export function LanguageDialog({ setEnglish, setBengali }) {

    return (
        <Dialog open>
            <DialogTitle sx={{p: 2}}>Choose Preferred Language</DialogTitle>
            <Divider />
            <Grid container justifyContent="space-around" item sx={{ p: 2 }}>
                <LanguageButton onClick={setEnglish} text="English" />
                <LanguageButton onClick={setBengali} text=" বাংলা "/>
            </Grid>
        </Dialog>
    );
}
