import * as React from "react";
import Box from "@mui/material/Box";
import Dialog from "@mui/material/Dialog";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";
import Typography from "@mui/material/Typography";
import Link from "@mui/material/Link";
import bday_bg from "../../images/birthday_bg.jpg";

import MemorySwipeableView from "../../components/memory_swipeable_view.jsx";

const CloseButton = ({ onClick }) => (
    <IconButton
        edge="start"
        onClick={onClick}
        aria-label="close"
        sx={{
            fontSize: "3.5rem",
            position: "absolute",
            top: 20,
            right: 20,
            bgcolor: "#ffffff",
        }}
    >
        <CloseIcon sx={{ fontSize: "3rem", fill: "muted.main" }} />
    </IconButton>
);

export const ImageButton = ({ children, onClick, borderColor }) => (
    <Link
        sx={{
            color: "button.main",
            border: 2,
            borderColor: borderColor || "transparent",
            borderRadius: 5,
        }}
        variant="outlined"
        onClick={onClick}
    >
        {children}
    </Link>
);

const MemoryDialog = ({ text, open, onClose, onClick, children }) => (
    <Dialog
        fullScreen
        open={open}
        onClose={onClose}
        sx={{ backgroundImage: `url(${bday_bg})` }}
    >
        {children}
        <Typography
            variant="h5"
            sx={{
                width: "fit-content",
                position: "absolute",
                right: 0,
                left: 20,
                top: 20,
                color: "#fff",
                border: 2,
                borderColor: "secondary.main",
                p: 1,
                borderRadius: 5,
                bgcolor: "muted.main",
            }}
        >
            {text}
        </Typography>
        <CloseButton onClick={onClick} />
    </Dialog>
);

export function ImageLink({ language, picnum, children, details, context }) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <Box>
            <ImageButton onClick={handleClickOpen}>{children}</ImageButton>
            <MemoryDialog
                text={details.title[language]}
                open={open}
                onClose={handleClose}
                onClick={handleClose}
            >
                <MemorySwipeableView
                    picnum={picnum}
                    details={details}
                    images={context}
                />
            </MemoryDialog>
        </Box>
    );
}
