import Grid from "@mui/material/Grid";
import Link from "@mui/material/Link";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
    faYoutube,
    faFacebook,
    faTwitter,
    faInstagramSquare,
} from "@fortawesome/free-brands-svg-icons";

import { FooterText, SocialLink } from "./footer/utils";

const YoutubeIcon = (props) => <FontAwesomeIcon icon={faYoutube} {...props} />;

const FacebookIcon = (props) => (
    <FontAwesomeIcon icon={faFacebook} {...props} />
);

const TwitterIcon = (props) => <FontAwesomeIcon icon={faTwitter} {...props} />;

const InstagramIcon = (props) => (
    <FontAwesomeIcon icon={faInstagramSquare} {...props} />
);

export const YoutubeLink = ({ size, color }) => (
    <SocialLink
        to="https://www.youtube.com/channel/UCabGr_w5PLFIg1rcmKPbeVg"
        color={color}
    >
        <YoutubeIcon size={size} />
    </SocialLink>
);

export const FacebookLink = ({ size, color }) => (
    <SocialLink to="https://www.facebook.com/mymemorystore.in" color={color}>
        <FacebookIcon size={size} />
    </SocialLink>
);

export const TwitterLink = ({ size, color }) => (
    <SocialLink to="https://twitter.com/mymymemorystore/" color={color}>
        <TwitterIcon size={size} />
    </SocialLink>
);

export const InstagramLink = ({ size, color }) => (
    <SocialLink to="https://www.instagram.com/mymemorystore.in/" color={color}>
        <InstagramIcon size={size} />
    </SocialLink>
);

export default function SocialLinks(props) {
    return (
        <Grid item>
            <FooterText>Follow Us</FooterText>
            <YoutubeLink color="social.youtube" {...props} />
            <FacebookLink color="social.facebook" {...props} />
            <TwitterLink color="social.twitter" {...props} />
            <InstagramLink color="social.instagram" {...props} />
        </Grid>
    );
}

export function SocialIcon(props) {
    return (
        <Link
            href={props.target}
            rel="noreferrer noopener"
            target="_blank"
            sx={{ paddingX: 1 }}
        >
            <FontAwesomeIcon
                icon={props.icon}
                className={props.cls}
                {...props}
            />
        </Link>
    );
}
